export default {
  overlay : {
    position          : 'fixed',
    top               : 0,
    left              : 0,
    right             : 0,
    bottom            : 0,
    backgroundColor   : 'rgba(26,35,51,.5)'
  },
  content : {
    position                   : null,
    top                        : null,
    left                       : null,
    right                      : null,
    bottom                     : null,
    border                     : '3px solid #e6e7e5',
    background                 : '#ffffff',
    overflow                   : 'auto',
    WebkitOverflowScrolling    : 'touch',
    borderRadius               : '4px',
    outline                    : 'none',
    padding                    : '20px'
  }
};



